body {
  color: #333;
  background-color: #f1f1f1;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  color: #3498db;
  margin: 30px 0;
  font-size: 24px;
}

.loader {
  display: none;
}

.loader:after {
  content: "";
  width: 50px;
  height: 50px;
  border: 5px solid #ccc;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 20px auto;
  animation: spin 2s linear infinite;
  display: block;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  text-align: center;
  margin-top: 20px;
  font-size: 16px;
  display: none;
}

.cat-info {
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  margin-top: 20px;
  padding: 20px;
  display: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.cat-info p {
  margin-bottom: 10px;
  font-size: 16px;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-top: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.breed-select {
  width: 100%;
  max-width: 300px;
  color: #333;
  background-color: #f8f8f8;
  border: 2px solid #3498db;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px;
  font-size: 16px;
  display: none;
}

.loader, .error {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader.active, .error.active, .cat-info.active {
  display: block;
}

